.container{
    display: flex;
    flex-direction: row;
    width: 90%;
    height: 100vh;
    padding-left: 5%;
    padding-right: 5%;
}
.sidebar{
    width: 20%;
    height: 100%;
    flex-grow: 1;
    padding: 0;
    margin: 0;
}
.main{
    margin-top: 5%;
    margin-bottom: 5%;
    width: 80%;
    height: 100vh;
    flex-grow: 5;
}
.question{
    margin-top: 1em;
    margin-bottom: 1em;
}
.options{
    list-style-type: none;
    margin-bottom: 1em;
}
.sub-option{
    margin-bottom: 1em;
}
.primaryBtn{
    -webkit-font-smoothing:antialiased;
    background-color: #32CD32!important;
    color: #ffffff;
    white-space:nowrap;
    -moz-border-radius:5px;
    -webkit-border-radius:5px;
    border-radius:5px;
    border:3px solid rgba(255,255,255,0.23);
    -moz-box-shadow:0px 1px 2px rgba(0,0,0,0.14);
    -webkit-box-shadow:0px 1px 2px rgba(0,0,0,0.14);
    -webkit-transition:all 0.1s ease;
    -ms-transition:all 0.1s ease;
    -moz-transition:all 0.1s ease;
    -o-transition:all 0.1s ease;
    transition:all 0.1s ease;
    height: 3em;
    width: 70%;
    cursor: pointer;
    font-size: 18px;
    font-weight: 500;
}
.list-stuff {
    color: #333;
    display: flex;
    flex-direction: row;
    padding: 0;
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 1em;
  }
.li {
    display: inline-block;
    font-size: 0.9em;
    list-style-type: none;
    padding: 0.5em;
    text-transform: uppercase;
  }
  
.li span {
    display: flex;
    padding: 0;
    margin: 0;
    font-size: 2rem;
  }